import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Modal, Button } from 'react-bootstrap';
import { motion, AnimatePresence } from 'framer-motion';
import SearchCheckbox from '../../components/searchCheckbox/SearchCheckbox';
import { optionMetrics } from '../../constants/filterOptions';
import { ReactComponent as Loader } from '../../assets/icons/loader.svg';
import { ReactComponent as Asia } from '../../assets/icons/asia.svg';
import { ReactComponent as Building } from '../../assets/icons/building.svg';
import { ReactComponent as Flag } from '../../assets/icons/flag.svg';
import { ReactComponent as Globe } from '../../assets/icons/globe.svg';
import { ReactComponent as Map } from '../../assets/icons/map.svg';
import { ReactComponent as Profile } from '../../assets/icons/profile.svg';
import { ReactComponent as Team } from '../../assets/icons/team.svg';
import './snapshot.scss';

const animateStep = {
  visible: {
    opacity: 1,
    x: '0',
    transition: { duration: .5, bounce: 0 }
  },
  backEnter: {
    opacity: 0,
    x: '-100px',
    transition: { duration: .5, bounce: 0 }
  },
  backExit: {
    opacity: 0,
    x: '100px',
    transition: { duration: .5, bounce: 0 }
  },
  nextEnter: {
    opacity: 0,
    x: '100px',
    transition: { duration: .5, bounce: 0 }
  },
  nextExit: {
    opacity: 0,
    x: '-100px', 
    transition: { duration: .5, bounce: 0 }
  }
}

const animateGenerate = {
  hidden: {
    opacity: 0
  },
  visible: {
    opacity: 1
  }
}

const BuilderModal = (props) => {
  const { showBuilderModal, onCancelModal } = props;
  let history = useHistory();
  const [stepOpen, setStepOpen] = useState(1);
  const [clickBack, setClickBack] = useState(false);
  const [clickNext, setClickNext] = useState(false);
  const [generating, setGenerating] = useState(false);

  const onCancel = async () => {
    await setClickBack(false)
    await setClickNext(false)
    await onCancelModal()
    setStepOpen(1)
  }

  const onBack = async () => {
    await setClickNext(false)
    await setClickBack(true)
    setStepOpen(stepOpen-1)
  }

  const onNext = async () => {
    await setClickBack(false)
    await setClickNext(true)
    setStepOpen(stepOpen+1)
  }

  const runReport = () => {
    setGenerating(true);
    setStepOpen(5)
    // API here
    setTimeout(async()=>{
      setGenerating(false)
    }, 5000)
  }

  const viewReport = () => {
    history.push('/snapshot-builder/view-report')
  }

  return (
    <>

      <Modal
        show={showBuilderModal}
        onHide={onCancel}
        className="builder-modal"
        backdrop="static"
        keyboard={false}
        centered
      >
        <div className="to-prevent-glitch">prevent glitch</div>
        <div className="content-wrapper">
          <AnimatePresence exitBeforeEnter>
            {stepOpen === 1 && (
              <motion.div className={`content-container step-${stepOpen}`}
                key={stepOpen}
                variants={animateStep}
                initial={clickBack ? 'backEnter' : clickNext ? 'nextEnter' : false}
                animate='visible'
                exit='nextExit'
              >
                <Modal.Header>
                  <Modal.Title>STEP 1: CHOOSE A REPORT TYPE</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="btn-container"><Button variant="link"><Building className="icon"/>GLOBAL REPORT</Button></div>
                    <div className="btn-container"><Button variant="link"><Flag className="icon"/>REGIONAL REPORT</Button></div>
                    <div className="btn-container"><Button variant="link"><Globe className="icon"/>COUNTRY REPORT</Button></div>
                    <div className="btn-container"><Button variant="link"><Map className="icon"/>METRO REPORT</Button></div>
                    <div className="btn-container"><Button variant="link"><Profile className="icon"/>SALES TEAM REPORT</Button></div>
                    <div className="btn-container"><Button variant="link"><Team className="icon"/>CUSTOMER REPORT</Button></div>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </motion.div>
            )}
            {stepOpen === 2 && (
              <motion.div className={`content-container step-${stepOpen}`}
                key={stepOpen}
                variants={animateStep}
                initial={clickBack ? 'backEnter' : clickNext ? 'nextEnter' : false}
                animate='visible'
                exit={clickBack ? 'backExit' : clickNext ? 'nextExit' : false}
              >
                <Modal.Header>
                  <Modal.Title>STEP 2: CHOOSE A REGION</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                  <div className="btn-container"><Button variant="link"><Asia className="icon"/>AMERICAS</Button></div>
                  <div className="btn-container"><Button variant="link"><Asia className="icon"/>EUROPE, MIDDLE EAST, AND AFRICA</Button></div>
                  <div className="btn-container"><Button variant="link"><Asia className="icon"/>ASIA PACIFIC</Button></div>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </motion.div>
            )}
            {stepOpen === 3 && (
              <motion.div className={`content-container step-${stepOpen}`}
                key={stepOpen}
                variants={animateStep}
                initial={clickBack ? 'backEnter' : clickNext ? 'nextEnter' : false}
                animate='visible'
                exit={clickBack ? 'backExit' : clickNext ? 'nextExit' : false}
              >
                <Modal.Header>
                  <Modal.Title>STEP 3: CHOOSE TIMEFRAME</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                  <div className="btn-container"><Button variant="link">LAST COMPLETED QUARTER</Button></div>
                  <div className="btn-container"><Button variant="link">LAST YEAR</Button></div>
                  <div className="btn-container"><Button variant="link">YEAR-TO-DATE</Button></div>
                  <div className="btn-container"><Button variant="link">LAST 4 QUARTERS</Button></div>
                  <div className="btn-container"><Button variant="link">CUSTOM</Button></div>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => onNext()}>Next</Button>
                </Modal.Footer>
              </motion.div>
            )}
            {stepOpen === 4 && (
              <motion.div className={`content-container step-${stepOpen}`}
                key={stepOpen}
                variants={animateStep}
                initial={clickBack ? 'backEnter' : clickNext ? 'nextEnter' : false}
                animate='visible'
                exit={clickBack ? 'backExit' : clickNext ? 'nextExit' : false}
              >
                <Modal.Header>
                  <Modal.Title>STEP 4: CHOOSE METRICS</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                  <SearchCheckbox
                    options={optionMetrics}
                    searchPlaceholder='Search for Metric'
                  />
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="light" onClick={() => onBack()}>Back</Button>
                  <Button variant="light" onClick={() => onCancel()}>Cancel</Button>
                  <Button variant="info" onClick={() => runReport()}>Run Report</Button>
                </Modal.Footer>
              </motion.div>
            )}
            {stepOpen === 5 && (
              <motion.div className={`content-container step-${stepOpen}`}
                key={stepOpen}
                variants={animateStep}
                initial={clickBack ? 'backEnter' : clickNext ? 'nextEnter' : false}
                animate='visible'
                exit={clickBack ? 'backExit' : clickNext ? 'nextExit' : false}
              >
                <Modal.Body>
                  {generating 
                    ? <motion.div className="body-content"
                        key={generating}
                        variants={animateGenerate}
                        initial='hidden'
                        animate='visible'
                      >
                        <Loader className="icon mb-4"/>
                        <h5 className="title">Generating Report…</h5>
                        <p className="note">We’ll send the report to your email after we finish.</p>
                      </motion.div>
                    : <motion.div className="body-content"
                        key={generating}
                        variants={animateGenerate}
                        initial='hidden'
                        animate='visible'
                      >
                        <h5 className="title">Finished!</h5>
                        <Button variant="info" onClick={() => viewReport()}>View Report</Button>
                      </motion.div>
                  }
                </Modal.Body>
              </motion.div>
            )}
          </AnimatePresence>
        </div>
      </Modal>
    </>
  )
};


export default BuilderModal;
