import React, { useState } from 'react';
import PageHeader from '../../components/pageHeader/PageHeader';

import Navigator from './Navigator';

import './accountNavigator.scss';


const CustomerNavigator = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Navigator')
  }

  const breadcrumbs = [
    {
      path: '/account-navigator',
      label: 'Account Navigator'
    },
    {
      label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/account-navigator/navigator',
      label: 'Navigator'
    }
  ]

  return (
    <div className="customer-navigator-page">
      <PageHeader
        title="ACCOUNT NAVIGATOR"
        color="dark-orange"
        innerPage={innerPage}
        breadcrumbs={breadcrumbs}
        previousPage="/account-navigator"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'navigator' && <Navigator />}
      </div>
    </div>
  );
}

export default CustomerNavigator;
