import React from 'react';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';

import CustomTable from '../../components/customTable/CustomTable';


const NavigatorContent = (props) => {
  const { all_accounts, newly_added_accounts, world_wide_map } = props;

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: 'rank',
      text: 'RANK',
      align: 'left',
      headerAlign: 'left'
    },
    {
      dataField: 'customer_name',
      text: 'CUSTOMER NAME',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'is_customer',
      text: 'CUSTOMER',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'company_revenue',
      text: 'COMPANY REVENUE',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'mrr',
      text: 'MRR',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    }
  ]

  const defaultSorted = [
    {
      dataField: 'rank',
      order: 'asc', // or desc
    }
  ];

  return (
    <Container fluid>
      <Row className="contentDivider">
        <Col md={6}>
          <Card className="navigator-card">
            <Card.Body>

            </Card.Body>
          </Card>
        </Col>
        <Col md={6}>
          <Card className="navigator-card">
            <Card.Body>
              <Card.Title>Newly Added Accounts This Quarter</Card.Title>
                <CustomTable
                  tableName='navigator-table scrollable'
                  data={newly_added_accounts === undefined ? [] : newly_added_accounts}
                  defaultSorted={defaultSorted}
                  columns={columns}
                />
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingTop: "30px" }}>
        <Col md={6}>
          <Card className="navigator-card">
            <Card.Body>
              <Card.Title>Worldwide</Card.Title>
              
            </Card.Body>
          </Card>

        </Col>
        <Col md={6}>
          <Card className="navigator-card">
            <Card.Body>
              <Card.Title>All Accounts</Card.Title>
              <CustomTable
                  tableName='navigator-table scrollable'
                  data={all_accounts === undefined ? [] : all_accounts}
                  defaultSorted={defaultSorted}
                  columns={columns}
                />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>

  );
};

export default NavigatorContent;